:root {
  --font-overpass: "Overpass", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --lightgrey: hsl(217, 12%, 63%);
  --mediumgrey: hsl(216, 12%, 54%);
  --darkblue: hsl(213, 19%, 18%);
  --verydarkblue: hsl(216, 12%, 8%);
  --orange: hsl(25, 97%, 53%);
  --card-bg-top: hsl(215, 19%, 18%);
  --card-bg-bottom: hsl(215, 27%, 12%);
  --bgs-inside-card: hsl(210, 19%, 18%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

.rating__numbers-container.selected {
  background-color: var(--orange);
  color: var(--white);
}
.rating__numbers-container.selected:hover {
  background-color: var(--orange);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  font-family: var(--font-overpass);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

html,
body {
  height: 100%;
}

body {
  background-color: #141519;
  font-family: var(--font-overpass);
  display: flex;
  flex-direction: column;
}

.container {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

footer {
  margin: auto 0 0.75rem 0;
  color: var(--white);
}

.display {
  display: none !important;
}

.card {
  background: linear-gradient(to bottom, var(--card-bg-top), var(--card-bg-bottom));
  width: 25.75rem;
  height: 26.125rem;
  padding: 2.125rem;
  border-radius: 1.5rem;
}

.rating__star {
  background-color: var(--bgs-inside-card);
  padding: 0.9375rem;
  border-radius: 50%;
  margin-bottom: 1.5rem;
}
.rating__heading {
  font-size: 1.75rem;
  color: var(--white);
  margin-bottom: 1.25rem;
}
.rating__paragraph {
  font-size: 0.9375rem;
  line-height: 1.5rem;
  color: var(--lightgrey);
  margin-bottom: 1.5rem;
}
.rating__numbers {
  color: var(--lightgrey);
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.5rem;
}
.rating__numbers-container {
  font-weight: 700;
  width: 3.25rem;
  height: 3.25rem;
  border-radius: 50%;
  background-color: var(--bgs-inside-card);
  display: flex;
  justify-content: center;
  align-items: center;
}
.rating__numbers-container:hover {
  cursor: pointer;
  background-color: var(--mediumgrey);
  color: var(--white);
  transition: all 0.2s;
}
.rating__btn {
  font-size: 0.875rem;
  letter-spacing: 0.1875rem;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  color: var(--white);
  background-color: var(--orange);
  display: block;
  padding: 0.9375rem;
  border-radius: 62.4375rem;
}
.rating__btn:hover {
  cursor: pointer;
  color: var(--orange);
  background-color: var(--white);
  transition: all 0.2s;
}
.rating__error {
  margin: 0;
  margin-top: 0.75rem;
  text-align: center;
}

.thanks {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.thanks__illustration {
  margin-bottom: 1.5rem;
}
.thanks__selected {
  color: var(--orange);
  background-color: var(--bgs-inside-card);
  border-radius: 62.4375rem;
  padding: 0.375rem 1rem;
  margin-bottom: 2.5rem;
}
.thanks__heading {
  font-size: 1.75rem;
  color: var(--white);
  margin-bottom: 1rem;
}
.thanks__paragraph {
  font-size: 0.94375rem;
  line-height: 1.5rem;
  color: var(--lightgrey);
  text-align: center;
}

.attribution {
  font-size: 0.75rem;
  text-align: center;
}
.attribution a {
  color: var(--orange);
}
.attribution a:hover {
  color: rgba(251, 116, 19, 0.7);
  transition: all 0.2s;
}